body {
    font: 400 15px;
    font-family: Lato, sans-serif;
    line-height: 1.8;
    color: #818181;
}

.jumbotron {
    background-color: #34568b;
    color: #ffffff;
    padding: 100px 25px;
    font-family: Montserrat, sans-serif; 
}

.contact {
    font-size: xx-large;
}
.bg-grey {
    background-color: #f6f6f6;
}

.container-fluid {
    padding: 40px 40px;
}

.logo {
    font-size: 200px;
    color: #34568b
}

.logo-small {
    color: #34568b;
    font-size: 50px;
}

.list {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    padding-right: 0px;
    padding-left: 0px;
    list-style: none;
  }

  li {
    text-decoration: none;
  }

.thumbnail {
    padding: 0 0 15px 0;
    border: none;
    border-radius: 0;
    background-color: #fff;
}

.thumbnail img {
    width: 100%;
    height: 515px;
    margin-bottom: 10px;
}

#profile {
    width: 250px;
    height: 250px;
    border-radius: 25px;
}

.navbar {
    margin-bottom: 0;
    background-color: #34568b;
    z-index: 9999;
    border: 0;
    font-size: 12px !important;
    line-height: 1.42857143 !important;
    letter-spacing: 4px;
    border-radius: 0;
    font-family: Montserrat, sans-serif;
    position: sticky;
    top: 0;
}

.navbar li a, .navbar .navbar-brand {
    color: #fff !important;
}

h2 {
    font-size: 24px;
    text-transform: uppercase;
    color: #303030;
    font-weight: 600;
    margin-bottom: 30px;
}

h4 {
    font-size: 19px;
    line-height: 1.375em;
    color: #303030;
    font-weight: 400;
    margin-bottom: 30px;
}

.navbar-nav li a:hover, .navbar-nav li.active a {
    color: #34568b !important;
    background-color: #fff !important;
}

.navbar-default .navbar-toggle {
    border-color: transparent;
    color: #fff !important;
}

footer .glyphicon {
    font-size: 20px;
    margin-bottom: 20px;
    color: #34568b;
}

@media screen and (max-width: 768px) {
    .col-sm-4 {
      text-align: center;
      margin: 25px 0;
    }
}